import React, {FC} from "react";
import style from "./BurgerMenu.module.scss"
import clsx from "clsx";
import {translate} from "../../utils/lang";
import {HashLink} from "react-router-hash-link";
import {useSelector} from "react-redux";
import {selectLang, selectThemeType} from "../../store/reducers/app.reducer";

interface IBurgerMenu {
    showBurger: boolean
}

const links = [
    {
        to: "/",
        label: "about us"
    },
    {
        to: "/",
        label: "portfolio"
    },
    {
        to: "/",
        label: "shop"
    },
    {
        to: "/",
        label: "contacts"
    },
];


export const BurgerMenu: FC<IBurgerMenu> = ({showBurger}) => {
    const themeType = useSelector(selectThemeType);
    const lang = useSelector(selectLang);

    return (
        <div className={clsx({
            [style.burgerMenu]: true,
            [style.burgerMenu_show]: showBurger,
        })}>
            <div className={style.inner}>
                {
                    links.map(({to, label}, index) => (
                        <HashLink key={index}
                                  to={to}
                                  className={clsx({
                                      [style.link]: true,
                                      [style.link_light]: themeType === "light",
                                      [style.link_dark]: themeType === "dark",
                                  })}
                                  smooth={true}
                        >
                            {
                                translate(label, lang)
                            }
                        </HashLink>
                    ))
                }
            </div>
        </div>
    )
}