import style from "./Faq.module.scss";
import {Container} from "../common/Container/Container";
import React, {useState} from "react";
import {H2} from "../common/H2/H2";

const items = [
    {
        title: "How quickly will you make an order on the site in time?",
        text: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus.",
    },
    {
        title: "What do you need to do to order a site from you?",
        text: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus.",
    },
    {
        title: "What if I don't like the design?",
        text: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus.",
    },
    {
        title: "Where can I see your work?",
        text: "Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus.",
    },

]


export const Faq = () => {
    const [currentIndex, setCurrentIndex] = useState(0);

    return (
        <Container classNameInner={style.faq}>
            <H2 text="FAQ"/>

            <div className={style.cards}>
                {
                    items.map(({title, text}, index) => (
                        <div key={index}
                             className={style.item}
                             onClick={() => setCurrentIndex(index)}
                        >
                            <p className={style.title}>{title}</p>
                            {
                                currentIndex === index &&
                                <p className={style.text}>{text}</p>
                            }
                        </div>
                    ))
                }
            </div>
        </Container>
    )
}